import {CoinflowWithdraw, SolanaWallet} from '@coinflowlabs/react';
import {useConnection} from '@solana/wallet-adapter-react';
import {Wallet} from './Wallet';
import {PublicKey, Transaction} from '@solana/web3.js';
import {useEffect, useState} from 'react';

declare global {
  interface Window {
    sendToFlutter: (message: string) => Promise<string>;

    flutter_inappwebview: {
      callHandler: (handlerName: string) => Promise<any>;
    };
  }
}

interface CoinflowContentProps {
  publicKeyParam: string;
  clusterParam: string;
  emailParam: string;
}

interface InitData {
  rpcUrl: string;
  publicKey: string;
  cluster: string;
  email: string;
}

function App() {
  const [data, setData] = useState<InitData | null>(null);

  useEffect(() => {
    const fetchData = async () => {
      const data = await window.flutter_inappwebview?.callHandler('init');
      setData(data);
    };
    fetchData();
  }, []);

  if (!data) {
    return <Loading />;
  }

  return (
    <Wallet rpcUrl={data.rpcUrl}>
      <div className="App">
        <div
          style={{
            height: '100vh',
          }}
        >
          <CoinflowContent
            publicKeyParam={data.publicKey}
            clusterParam={data.cluster}
            emailParam={data.email}
          />
        </div>
      </div>
    </Wallet>
  );
}

function CoinflowContent({
  publicKeyParam,
  clusterParam,
  emailParam,
}: CoinflowContentProps) {
  const {connection} = useConnection();

  if (!publicKeyParam) {
    return <WalletNotFound />;
  }

  const publicKey = new PublicKey(publicKeyParam);

  const wallet: SolanaWallet = {
    publicKey,
    sendTransaction: async (transaction: Transaction) => {
      const encoded = transaction.serialize({
        requireAllSignatures: false,
        verifySignatures: false,
      });

      return window.sendToFlutter(encoded.toString('base64'));
    },
  };

  return (
    <CoinflowWithdraw
      wallet={wallet}
      connection={connection}
      merchantId={process.env.REACT_APP_MERCHANT_ID as string}
      env={clusterParam === 'mainnet' ? 'prod' : 'sandbox'}
      blockchain={'solana'}
      email={emailParam}
    />
  );
}

function WalletNotFound() {
  return <div style={{textAlign: 'center'}}>Wallet not found</div>;
}

function Loading() {
  return (
    <div
      style={{
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        height: '100vh',
      }}
    >
      <div>Loading...</div>
    </div>
  );
}

export default App;
